.aboutme {
  border-radius: 10px;
}
@-webkit-keyframes example {
    0%   {margin-left: -700px;}
    100% {margin-left: -1vw;}
}
@keyframes example {
    0%   {margin-left: -700px;}
    100% {margin-left: -1vw;}
}
.aboutme div {
  position: absolute;
  width: 14vw;
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  background-color: #0c1a2d;
  margin-left: -100px;
  height: 5vw;
  border-radius: 10px;
}

.aboutme h1 {
  text-align: center;
  padding-top: 1.1vw;
  font-size: 2vw;
  color: #e8efef;
  letter-spacing: 0.1vw;
  font-family: DinRegular;
}


.intro {
  /* align-items: center; */
  /* margin-left: 20vw; */
