  display: -ms-flexbox;
  display: flex;
  padding-top: 6vw;
  -ms-flex-direction: column;
      flex-direction: column;
  word-wrap: break-word;
}

.intro p:nth-child(2){
  margin-right: 25vw;
  margin-left: 25vw;
  padding-top: 0.7vw;
  word-wrap: break-word;
  /* color: #636868; */
  font-size: 1.3vw;
