}

#goku {
  border-radius: 50%;
  width: 8vw;
  height: 8vw;
  /* align-items: center;
  text-align: center; */
  margin-left: 28vw;
  margin-right: -2vw;
  border: 1px solid #C7AA5F;
  /* margin-left: 25vw;
  margin-right: 5px; */
  /* margin-top: 5vw; */
  -webkit-animation: glow 0.8s steps(50) infinite alternate;
          animation: glow 0.8s steps(50) infinite alternate;

}

#goku:hover{
  /* filter: grayscale(0%);
  filter:invert(0.1); */
  -webkit-box-shadow:
  0 0 1px 3px #F9D477,  /* inner white */
  0 0 30px 10px #F9D477, /* middle magenta */
  0 0 10px 7px #C7AA5F;
          box-shadow:
  0 0 1px 3px #F9D477,  /* inner white */
  0 0 30px 10px #F9D477, /* middle magenta */
  0 0 10px 7px #C7AA5F; /* outer cyan */
}

@-webkit-keyframes glow {
  0% { -webkit-box-shadow: none; box-shadow: none; }
  100% { -webkit-box-shadow:
  0 0 1px 3px #F9D477,  /* inner white */
  0 0 30px 10px #F9D477, /* middle magenta */
  0 0 10px 7px #C7AA5F; box-shadow:
  0 0 1px 3px #F9D477,  /* inner white */
  0 0 30px 10px #F9D477, /* middle magenta */
  0 0 10px 7px #C7AA5F; /* outer cyan */ }
}

@keyframes glow {
  0% { -webkit-box-shadow: none; box-shadow: none; }
  100% { -webkit-box-shadow:
  0 0 1px 3px #F9D477,  /* inner white */
  0 0 30px 10px #F9D477, /* middle magenta */
  0 0 10px 7px #C7AA5F; box-shadow:
  0 0 1px 3px #F9D477,  /* inner white */
  0 0 30px 10px #F9D477, /* middle magenta */
  0 0 10px 7px #C7AA5F; /* outer cyan */ }
}


